<template>
<div style="width:100%;height:100%">
    <div class="index-left">
      <menu-common :activeIndex="activeIndex"></menu-common>
    </div>
    <div class="index-right">
      <index-nav-top></index-nav-top>
      <div class="index-right-section">
        <div class="section-content">
          <div class="section-content-top">
            <div class="common-marker fl">
            </div>
            <p class="fl">创建商品</p>
          </div>
          <div class="section-content-body">
            <el-form :model="postData"  ref="postData" label-width="100px">
            <el-row class="common-row-space">
              <el-col :span="24">
                <el-col :span="3" class="text-r common-line-height">商品名称：</el-col>
                <el-col :span="20">
                  <el-input v-model="postData.num" size="medium" class="common-long-length" placeholder="请输入商品名称"></el-input>
                </el-col>
              </el-col>
            </el-row>
            <el-row class="common-row-space">
              <el-col :span="24">
                <el-col :span="3" class="text-r common-line-height">分类：</el-col>
                <el-col :span="20">
                  <el-select v-model="postData.num" placeholder="请选择" class="common-long-length">
                    <el-option
                      v-for="item in options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                </el-col>
              </el-col>
            </el-row>

            <el-row class="common-row-space">
              <el-col :span="24">
                <el-col :span="3" class="text-r common-line-height">介绍：</el-col>
                <el-col :span="20">
                  <el-input v-model="postData.num" type="textarea"
                            :autosize="{minRows: 4, maxRows: 4}" class="common-long-length" placeholder="请输入商品介绍"></el-input>
                </el-col>
              </el-col>
            </el-row>

            <el-row class="common-row-space">
              <el-col :span="24">
                <el-col :span="3" class="text-r common-line-height">商品价格：</el-col>
                <el-col :span="20">
                  <el-input v-model="postData.num" size="medium" class="common-length" placeholder="请输入商品价格"></el-input>
                  元
                </el-col>
              </el-col>
            </el-row>

            <el-row class="common-row-space">
              <el-col :span="24">
                <el-col :span="3" class="text-r common-line-height">总库存：</el-col>
                <el-col :span="20">
                  <el-input v-model="postData.num" size="medium" class="common-length" placeholder="请输入库存"></el-input>
                  件
                </el-col>
              </el-col>
            </el-row>


            <el-row class="common-row-space">
              <el-col :span="24">
                <el-col :span="3" class="text-r common-line-height">banner图片：</el-col>
                <el-col :span="20">


                  <el-upload
                    action="/share/tagDataMarket/uploadImage.ajax"
                    :on-success="uploadShowImageSuccess"
                    :on-error="uploadShowImageError"
                    list-type="picture-card"
                    :on-preview="handlePictureCardPreview"
                    :on-remove="handleShowImageRemove"
                    :limit="3"
                     accept="image/jpeg,image/jpg,image/png"
                  >
                    <i class="el-icon-plus"></i>
                    <div slot="tip" class="el-upload__tip"></div>

                  </el-upload>
                  <el-dialog :visible.sync="dialogVisible">
                    <img width="100%" :src="dialogImageUrl" alt="">
                  </el-dialog>

                </el-col>
              </el-col>
            </el-row>

            <el-row class="common-row-space">
              <el-col :span="24">
                <el-col :span="3" class="text-r common-line-height">详情页图片：</el-col>
                <el-col :span="20">
                  <el-upload
                    action="https://jsonplaceholder.typicode.com/posts/"
                    list-type="picture-card"
                    :on-preview="handlePictureCardPreviewDetail"
                    :on-remove="handleRemoveDetail">
                    <i class="el-icon-plus"></i>
                  </el-upload>
                  <el-dialog :visible.sync="dialogVisible">
                    <img width="100%" :src="dialogImageUrl" alt="">
                  </el-dialog>
                </el-col>
              </el-col>
            </el-row>



            <el-row class="common-row-space">
              <el-col :span="24">
                <el-col :span="3" class="text-r common-line-height">上下架时间：</el-col>
                <el-col :span="20">
                  <el-date-picker
                    v-model="postData.createdRange"
                    type="daterange"
                    align="right"
                    size="mini"
                    unlink-panels
                    range-separator="至"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期"
                    :picker-options="pickerOptions">
                  </el-date-picker>
                </el-col>
              </el-col>
            </el-row>



            <el-row class="common-row-space">
              <el-col :span="24">
                <el-col :span="3" class="text-r common-line-height">是否需要收货信息：</el-col>
                <el-col :span="20">
                  <el-select v-model="postData.num" placeholder="请选择" class="common-long-length">
                    <el-option
                      v-for="item in options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                </el-col>
              </el-col>
            </el-row>


            <el-row class="common-row-space">
              <el-col :span="24">
                <el-col :span="3" class="text-r common-line-height">商品属性：</el-col>
                <el-col :span="20">
                  <el-row>
                    <el-col :span="8">
                      <el-form-item inline=true label="属性名称：" prop="amount">
                        <el-input v-model="postData.amount" style="width: 160px" size="mini" clearable placeholder="请输入属性名称"></el-input>
                      </el-form-item>
                    </el-col>
                    <el-col :span="8">
                      <el-form-item inline=true label="金额：" prop="amount">
                        <el-input v-model="postData.amount" style="width: 160px" size="mini" clearable placeholder="请输入金额(单位：元)"></el-input>
                      </el-form-item>
                    </el-col>

                    <el-col :span="8">
                      <el-form-item inline=true label="个数：" prop="amount">
                        <el-input v-model="postData.amount" style="width: 160px" size="mini" clearable placeholder="请输入个数(单位：元)"></el-input>
                      </el-form-item>
                    </el-col>

                  </el-row>
                </el-col>
              </el-col>
            </el-row>


            <el-row class="common-row-space">
              <el-col :span="24">
                <el-col :span="3" class="text-r common-line-height">详细介绍：</el-col>
                <el-col :span="20">
                  <el-input v-model="postData.num" type="textarea"
                            :autosize="{minRows: 4, maxRows: 4}" class="common-long-length" placeholder="请输入商品详细介绍（展示在详情页面）"></el-input>
                </el-col>
              </el-col>
            </el-row>

            <el-row class="common-row-space" style="margin-top: 50px;">
              <el-button type="warning" style="margin-left:500px;">确定</el-button>
              <el-button style="margin-left:30px;">取消</el-button>
            </el-row>
            </el-form>
          </div>
        </div>
      </div>
    </div>
</div>
</template>

<script>
import MenuCommon from '../../components/menu'
import IndexNavTop from '../../components/indexNavTop'
export default {
  components: {
    IndexNavTop,
    MenuCommon},
  name: 'Index',
  data () {
    return {
      activeIndex: '1-1',
      options: [],
      postData: {
        num: ''
      },
      dialogImageUrl: '',
      dialogVisible: false
    }
  },
  methods: {
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    handleRemoveDetail(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreviewDetail(file) {
      alert(this.dialogImageUrl)
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;

    },


    uploadShowImageSuccess(res,file,fileList){
      //this.tagDataMarket.showImage = res.data.fileUrl;
    },
    uploadShowImageError(err,file){
      this.$message.error('上传失败')
    },
    uploadResultExmableSuccess(res,file,fileList){
     // this.tagDataMarket.resultImage = res.data.fileUrl;
    },
    uploadResultExmableError(err,file){
      this.$message.error('上传失败')
    },
    handleShowImageRemove(file,fileList){
      //this.tagDataMarket.showImage = '';
    },




  }
}
</script>
<style scoped>
.index-left {
  width:240px;height:100%;
  float:left;
}
.index-right{
  height:100%;
  margin-left: 239px;
}
.section-content{
  padding: 16px 24px;
  background-color: #ffffff;
  border-radius: 3px;
  min-height: 100%;
}
</style>



